export default {
  data() {
    return {
      activeName: "",
      ruleForm: {
        dizhu: "",
        cost: "",
        max: "",
        min: "",
        grab_banker_times: "",
        type_id: "",
        name: "",
        ip_limit: "",
        open_game: "",
        open_robot: "",
        robot_type: "",
        is_hundred_game: "",
      },
      rules: {
         dizhu: [{ required: true, message: "不可以为空", trigger: "blur" }],
         cost: [{ required: true, message: "不可以为空", trigger: "blur" }],
         max: [{ required: true, message: "不可以为空", trigger: "blur" }],
         min: [{ required: true, message: "不可以为空", trigger: "blur" }],
         grab_banker_times: [{ required: true, message: "不可以为空", trigger: "blur" }],
         type_id: [{ required: true, message: "不可以为空", trigger: "blur" }],
         ip_limit: [{ required: true, message: "不可以为空", trigger: "blur" }],
         open_game: [{ required: true, message: "不可以为空", trigger: "blur" }],
         open_robot: [{ required: true, message: "不可以为空", trigger: "blur" }],
      },
      //房间配置的所有数据
      allData:{},
      //当前游戏房间的所有数据
      currentlist:{},
      //匹配当前游戏的条件
      namelist:['200','201','202','203'],
      //游戏场次类别
      labellist:[],
      id: 0,
      keys: "",
      loading: false,
    };
  },
  async created() {
      let { data } = await this.$http.HallFunConfig.GetServerConfig({
      key: "roomdata.lua"
    });
    // console.log(data);
     this.id = data.data[0].id;
    this.keys = data.data[0].sys_key;
    let res = JSON.parse(data.data[0].sys_val);
    // console.log(res);
    this.allData = res
    this.namelist.forEach((item,index)=>{
      Object.keys(res).forEach((it)=>{
        if(item === it){
          this.currentlist[item] = res[item]
          this.currentlist[item].grab_banker_times = res[item].grab_banker_times.join('|')
        }
        if(index === 0){
          this.ruleForm = res[item]
          this.activeName = item
        }
      })
      this.labellist.push(res[item].name)
    })
  
    
    // console.log(this.currentlist,this.activeName,this.ruleForm,this.labellist,this.allData );
    
  },
  methods: {
    handleClick(tab) {
     this.namelist.forEach((item)=>{
       if(item === tab.name){
         this.ruleForm = this.currentlist[item]
       }
     })
    },
    submitForm(formName,type) {
        this.$refs[formName].validate(async valid => {
        if (valid) {
          // console.log(this.ruleForm,this.currentlist,this.allData);
          let all = JSON.parse(JSON.stringify(this.allData))
          this.namelist.forEach((item)=>{
            Object.keys(all).forEach((it)=>{
              if(item === it){
                all[it].grab_banker_times = all[it].grab_banker_times.split('|')
              }
            })
          })
          // console.log(all);
      if(type === 1){
        //发送put
         let { data } = await this.$http.HallFunConfig.PutServerConfig({
              keys: this.keys,
              values: JSON.stringify(all),
              id: this.id
            });
            // console.log(data);
            if (data.code === 1 && data.msg === "ok") {
              this.$message({
                type: "success",
                message: "保存成功!"
              });
            }
      }else if(type === 2){
        //发送post
          this.loading = true;

            let { data } = await this.$http.HallFunConfig.PostServerConfig({
              keys: this.keys,
              values: JSON.stringify(all),
              id: this.id
            });
            // console.log(data);
            if (data.code === 1 && data.msg === "ok") {
              this.loading = false;
              this.$message({
                type: "success",
                message: "发送服务器配置成功!"
              });
            }
      }

        }else{
            console.log("error submit!!");
          return false;
        }
        })
   
      
      

    },
  }
};



